import React, { useEffect } from 'react';
import propTypes from 'prop-types';
import { Button } from '../../components';
import { changeImage } from '../../utils';

const PortfolioCard = (props) => {
    const { currentIndex, setCurrentIndex } = props;

    const className = [props.filter];

    const copyToClipboard = (e) => {
        let link = e.target.dataset.link;

        navigator.clipboard.writeText(link);

        e.target.parentElement.classList.add('checked');
        e.target.classList.replace('uil-link-h', 'uil-check');

        setTimeout(() => {
            e.target.parentElement.classList.remove('checked');
            e.target.classList.replace('uil-check', 'uil-link-h');
        }, 2000);
    };

    useEffect(() => {
        document.querySelectorAll('.zoom-icon').forEach((icon, index) =>
            icon.addEventListener('click', () => {
                document.querySelector('.portfolio').classList.add('open');
                document.body.classList.add('stopScrolling');
                setCurrentIndex(index);
                changeImage(currentIndex);
            })
        );
    }, []);

    return (
        <div className={`portfolio-card mix ${className.join(' ')}`}>
            <div className="portfolio-image">
                <img src={props.image} alt={props.imageAlt} />
                <div className="portfolio-overlay">
                    <span
                        className="portfolio-icon zoom-icon"
                        style={{
                            '--i': '0s',
                        }}
                    >
                        <i className="uil uil-search-plus"></i>
                    </span>
                    <a
                        className="portfolio-icon"
                        style={{
                            '--i': '0.15s',
                        }}
                        onClick={copyToClipboard}
                    >
                        <i className="uil uil-link-h copy-btn" data-link={props.link}></i>
                    </a>
                </div>
            </div>
            <div className="portfolio-desc">
                <h3>{props.title}</h3>
                <Button isSecondary isSmall location={props.link} target="_blank">
                    Show
                </Button>
            </div>
        </div>
    );
};

PortfolioCard.propTypes = {
    title: propTypes.string,
    link: propTypes.string,
    imageAlt: propTypes.string,
};

export default PortfolioCard;
