import React from 'react';
import './index.css';
import { Circle1, Dots, Person1, Square1 } from '../../assets';
import { Button } from '../../components';
import { Fade } from 'react-reveal';

const Showcase = () => {
    return (
        <section className="showcase-area" id="home">
            <img src={Square1} className="shape square" alt="square" />
            <div className="container">
                <Fade bottom delay={600} duration={2500} distance={'60px'}>
                    <div className="showcase-info">
                        <h3 className="sub-heading">Web Developer</h3>
                        <h1 className="heading">Zachrie Kurniawan</h1>
                        <p className="text">I am a freelance web developer. Let's work together.</p>
                        <div className="cta">
                            <Button location="https://api.whatsapp.com/send?phone=6281274786920" target="_blank">
                                Contact me
                            </Button>
                            <Button isSecondary location="#" target="_blank">
                                Download CV
                            </Button>
                        </div>
                    </div>
                </Fade>
                <Fade top delay={700} duration={2500} distance={'60px'}>
                    <div className="showcase-image">
                        <img src={Person1} className="person" alt="Person" />
                        <img src={Circle1} className="shape circle" alt="circle" />
                        <img src={Dots} className="shape dots" alt="dots" />
                    </div>
                </Fade>
            </div>
        </section>
    );
};

export default Showcase;
