import React, { useEffect } from 'react';
import { HamburgerMenu, Links, Logo } from '../../components';
import './index.css';

const Header = () => {
    const activeLink = () => {
        let sections = document.querySelectorAll('section[id]');
        let passedSections = Array.from(sections)
            .map((sct, i) => {
                return {
                    y: sct.getBoundingClientRect().top - document.querySelector('header').offsetHeight,
                    id: i,
                };
            })
            .filter((sct) => sct.y <= 0);

        let currSectionID = passedSections.at(-1).id;

        document.querySelectorAll('.nav-link').forEach((link) => link.classList.remove('active'));
        document.querySelectorAll('.nav-link')[currSectionID].classList.add('active');
    };

    const stickyNavbar = () => {
        if (window.scrollY > 0) return document.querySelector('header').classList.add('scrolled');
        return document.querySelector('header').classList.remove('scrolled');
    };

    useEffect(() => {
        activeLink();
        stickyNavbar();

        window.addEventListener('scroll', () => {
            activeLink();
            stickyNavbar();
        });

        document.querySelectorAll('.nav-link').forEach((link) => {
            link.addEventListener('click', () => {
                document.body.classList.remove('open');
                document.body.classList.remove('stopScrolling');
            });
        });
    }, []);

    return (
        <header>
            <nav className="container">
                <Logo />
                <Links />
                <HamburgerMenu />
            </nav>
        </header>
    );
};

export default Header;
