import React from 'react';
import { Button } from '../../components';
import './index.css';

const NotFound = () => {
    return (
        <div className="not-found-page">
            <h1 className="heading">Page Not Found</h1>
            <Button isRouterLink isSecondary location="/">
                Back to Home
            </Button>
        </div>
    );
};

export default NotFound;
