import React from 'react';
import './index.css';

const HamburgerMenu = () => {
    const menuToggle = () => {
        document.body.classList.toggle('open');
        document.body.classList.toggle('stopScrolling');
    };

    return (
        <div className="hamburger" onClick={menuToggle}>
            <div className="bar"></div>
            <div className="bar"></div>
        </div>
    );
};

export default HamburgerMenu;
