import React, { useRef } from 'react';
import './index.css';
import { Square2, Triangle } from '../../assets';
import { Button, FormInput } from '../../components';
import emailjs from '@emailjs/browser';
import Swal from 'sweetalert2';

const Contact = () => {
    const form = useRef();

    const sendEmail = (e) => {
        e.preventDefault();

        document.querySelector('.form-btn').textContent = 'Loading...';

        emailjs.sendForm('service_uxhva0n', 'template_34xlcft', form.current, 'user_boCqhBdLKwqmJisFcopKv').then(
            (result) => {
                Swal.fire({
                    icon: 'success',
                    title: 'Success!',
                    text: 'Your message has been sent successfully :D',
                    confirmButtonColor: '#6D33FF',
                });
                document.querySelector('.form-btn').textContent = 'Send';
            },
            (error) => {
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!',
                    confirmButtonColor: '#6D33FF',
                });
                document.querySelector('.form-btn').textContent = 'Send';
            }
        );

        document.querySelector('.contact-form').reset();
    };

    return (
        <section className="contact section" id="contact">
            <img src={Triangle} className="shape triangle" alt="Triangle" />
            <div className="container">
                <img src={Square2} className="shape square" alt="Square 2" />
                <div className="contact-info">
                    <h3 className="sub-heading">Contact me</h3>
                    <h1 className="heading">Let's work together</h1>
                    <p className="text">Lorem ipsum dolor sit amet consectetur adipisicing elit. Obcaecati, quod in natus nulla porro earum ad recusandae veniam rerum labore.</p>
                    <Button isMail location={'mailto:zachrie2005@gmail.com'}>
                        zachrie2005@gmail.com <i className="uil uil-arrow-right"></i>
                    </Button>
                </div>

                <form method="POST" ref={form} onSubmit={sendEmail} className="contact-form">
                    <h3>Send me a message</h3>
                    <FormInput type="hidden" name="contact_number" />
                    <FormInput type="text" name="user_name" placeholder="Your Name" isRequired />
                    <FormInput type="text" name="user_subject" placeholder="Your Subject" isRequired />
                    <FormInput type="email" name="user_email" placeholder="Your Email" isRequired />
                    <FormInput name="message" placeholder="Project Details" isTextArea isRequired />
                    <Button isForm>Send</Button>
                </form>
            </div>
        </section>
    );
};

export default Contact;
