import React, { useEffect, useState } from 'react';
import mixitup from 'mixitup';
import { FilterButton, PortfolioCard } from '../../components';
import { changeImage } from '../../utils';
import { Portfolio1, Portfolio2, Portfolio3 } from '../../assets';
import './index.css';

const Portfolio = () => {
    const [currentIndex, setCurrentIndex] = useState(0);

    const modalToggle = () => {
        document.querySelector('.portfolio').classList.remove('open');
        document.body.classList.remove('stopScrolling');
    };

    const prevImage = () => {
        if (currentIndex === 0) {
            setCurrentIndex(document.querySelectorAll('.portfolio .images img').length - 1);
        } else {
            setCurrentIndex(currentIndex - 1);
        }
        changeImage(currentIndex);
    };

    const nextImage = () => {
        if (currentIndex === document.querySelectorAll('.portfolio .images img').length - 1) {
            setCurrentIndex(0);
        } else {
            setCurrentIndex(currentIndex + 1);
        }
        changeImage(currentIndex);
    };

    useEffect(() => {
        let mixer = mixitup('.portfolio-gallery', {
            selectors: {
                target: '.portfolio-card',
            },
            animation: {
                duration: 500,
            },
        });
    }, []);

    return (
        <section className="portfolio section" id="portfolio">
            <div className="modal">
                <div className="modal-overlay" onClick={modalToggle}></div>
                <div className="slider-wrap">
                    <div className="prev-btn navigation" onClick={prevImage}>
                        <i className="uil uil-previous"></i>
                    </div>
                    <div className="images">
                        <img src={Portfolio1} alt="Portfolio 1" />
                        <img src={Portfolio2} alt="Portfolio 2" />
                        <img src={Portfolio3} alt="Portfolio 3" />
                    </div>
                    <div className="next-btn navigation" onClick={nextImage}>
                        <i className="uil uil-step-forward"></i>
                    </div>
                </div>
            </div>
            <div className="container">
                <div className="portfolio-header">
                    <div className="portfolio-title">
                        <h3 className="sub-heading">Portfolio</h3>
                        <h1 className="heading">Latest Projects</h1>
                    </div>
                    <div className="filter-btns">
                        <FilterButton filterName="All" filterData="all" />
                        <FilterButton filterName="Product" filterData=".product" />
                        <FilterButton filterName="Interacting" filterData=".interacting" />
                        <FilterButton filterName="Web" filterData=".web" />
                    </div>
                </div>
                <div className="portfolio-gallery">
                    <PortfolioCard
                        title="Zach Network"
                        link="http://zach-network.000webhostapp.com/"
                        image={Portfolio1}
                        imageAlt="Portfolio 1"
                        filter="interacting web"
                        currentIndex={currentIndex}
                        setCurrentIndex={setCurrentIndex}
                    />
                    <PortfolioCard
                        title="Belajar Ngaji"
                        link="https://zachriek.github.io/belajar-ngaji/"
                        image={Portfolio2}
                        imageAlt="Portfolio 2"
                        filter="product"
                        currentIndex={currentIndex}
                        setCurrentIndex={setCurrentIndex}
                    />
                    <PortfolioCard
                        title="Hefa Store"
                        link="https://zachriek.github.io/hefa-store/"
                        image={Portfolio3}
                        imageAlt="Portfolio 3"
                        filter="product"
                        currentIndex={currentIndex}
                        setCurrentIndex={setCurrentIndex}
                    />
                </div>
            </div>
        </section>
    );
};

export default Portfolio;
