import React from 'react';
import './index.css';
import { Button, Logo } from '../../components';

const Footer = () => {
    return (
        <footer className="footer">
            <div className="container">
                <Logo />
                <p className="text">&copy; Copyright 2022. All rights reserved</p>
                <ul className="social-media">
                    <li>
                        <Button isSocial location="https://www.youtube.com/channel/UCFbzQWPGA17_gKzotx207jQ" target="_blank">
                            <i className="uil uil-youtube"></i>
                        </Button>
                    </li>
                    <li>
                        <Button isSocial location="https://github.com/zachriek" target="_blank">
                            <i className="uil uil-github"></i>
                        </Button>
                    </li>
                    <li>
                        <Button isSocial location="https://www.instagram.com/zachriek/" target="_blank">
                            <i className="uil uil-instagram"></i>
                        </Button>
                    </li>
                </ul>
            </div>
        </footer>
    );
};

export default Footer;
