import React, { useEffect } from 'react';
import { Button, SkillBox } from '../../components';
import { Dots, Square1 } from '../../assets';
import { hasReached } from '../../utils';
import './index.css';

const Skills = (props) => {
    const skillsCounter = () => {
        if (!hasReached(document.querySelector('.skill:first-child'))) return;

        document.querySelectorAll('.counter span').forEach((counter, i) => {
            let target = counter.dataset.target;
            let strokeValue = 0;

            switch (target) {
                case 'Beginner':
                    strokeValue = 250;
                    break;

                case 'Intermediate':
                    strokeValue = 120;
                    break;

                case 'Advanced':
                    strokeValue = 60;
                    break;

                default:
                    strokeValue = 0;
            }

            document.querySelectorAll('.skills svg circle')[i].style.setProperty('--target', strokeValue);
        });

        document.querySelectorAll('.skills svg circle').forEach((p) => {
            p.style.animation = 'progress 2s ease-in-out forwards';
        });
    };

    useEffect(() => {
        window.onscroll = () => {
            skillsCounter();
        };
    }, []);

    return (
        <section className="skills section">
            <div className="container">
                <div className="skills-box">
                    <div className="box-heading">
                        <h3 className="sub-heading">My skills</h3>
                        <h1 className="heading">Let me help you</h1>
                        <div className="box-desc">
                            <p className="text">
                                Lorem ipsum dolor sit amet consectetur adipisicing elit. Eum officia aspernatur nisi perferendis natus, ratione commodi mollitia! Molestiae laboriosam iusto architecto
                                sit aut, alias corrupti placeat aperiam, ex earum neque animi accusantium cum natus voluptas sequi illo veritatis dolorem consequatur?
                            </p>
                            <Button location="https://api.whatsapp.com/send?phone=6281274786920" target="_blank">
                                Contact Me
                            </Button>
                        </div>
                    </div>
                    <div className="skills-wrap">
                        {props.skillsData.map((skill, index) => (
                            <SkillBox skillName={skill.name} skillLevel={skill.level} key={index} />
                        ))}
                    </div>
                    <img src={Dots} className="shape dots" alt="Dots" />
                    <img src={Square1} className="shape square" alt="Square" />
                </div>
            </div>
        </section>
    );
};

export default Skills;
