import React from 'react';
import './index.css';
import propTypes from 'prop-types';
import { Link } from 'react-router-dom';

const Button = (props) => {
    const className = [];

    if (props.isSecondary) className.push('secondary-btn');
    if (props.isSmall) className.push('sm');

    if (props.isForm) {
        return (
            <button className="btn form-btn" type="submit">
                {props.children}
            </button>
        );
    }

    if (props.isMail) {
        return (
            <a href={props.location} className="mail">
                {props.children}
            </a>
        );
    }

    if (props.isSocial) {
        if (props.target === '_blank') {
            return (
                <a className="social-link" href={props.location} target={props.target} rel="noreferrer">
                    {props.children}
                </a>
            );
        }
        return (
            <a className="social-link" href={props.location}>
                {props.children}
            </a>
        );
    }

    if (props.target === '_blank') {
        return (
            <a href={props.location} className={`btn ${className.join(' ')}`} target={props.target} rel="noreferrer">
                {props.children}
            </a>
        );
    }

    if (props.isRouterLink) {
        if (props.target === '_blank') {
            return (
                <Link to={props.location} className={`btn ${className.join(' ')}`} target={props.target} rel="noreferrer">
                    {props.children}
                </Link>
            );
        }
        return (
            <Link to={props.location} className={`btn ${className.join(' ')}`}>
                {props.children}
            </Link>
        );
    }

    return (
        <a href={props.location} className={`btn ${className.join(' ')}`}>
            {props.children}
        </a>
    );
};

Button.propTypes = {
    location: propTypes.string,
    target: propTypes.string,
    className: propTypes.string,
    isSecondary: propTypes.bool,
    isSmall: propTypes.bool,
    isMail: propTypes.bool,
    isForm: propTypes.bool,
    isSocial: propTypes.bool,
    isRouterLink: propTypes.bool,
};

export default Button;
