import React from 'react';
import { Header, Showcase, Skills, Portfolio, Contact, Footer } from '../../parts';
import { skillsData } from '../../data';
import '../../index.css';

const Homepage = () => {
    return (
        <div>
            <div className="overlay"></div>
            <Header />
            <main>
                <Showcase />
                <Skills skillsData={skillsData} />
                <Portfolio />
                <Contact />
            </main>
            <Footer />
        </div>
    );
};

export default Homepage;
