import React from 'react';
import propTypes from 'prop-types';
import './index.css';

const FormInput = (props) => {
    if (props.isTextArea) {
        if (props.isRequired) {
            return <textarea className="form-input" name={props.name} placeholder={props.placeholder} required></textarea>;
        }

        return <textarea className="form-input" name={props.name} placeholder={props.placeholder}></textarea>;
    }

    if (props.isRequired) {
        return <input type={props.type} className="form-input" name={props.name} placeholder={props.placeholder} required />;
    }

    return <input type={props.type} className="form-input" name={props.name} placeholder={props.placeholder} />;
};

FormInput.propTypes = {
    type: propTypes.string,
    name: propTypes.string,
    placeholder: propTypes.string,
    isRequired: propTypes.bool,
    isTextArea: propTypes.bool,
};

export default FormInput;
